import { Injectable } from '@angular/core';

export interface Menu {
  state: string;
  name: string;
  type: string;
  icon: string;
}

const MENUITEMS = [
  {state: 'starterAdmin', name: '', type: 'link', icon: 'show_chart' },

  {state: 'manageUserAdmin', type: 'link', name: '', icon: 'person_pin'},
  {state: 'manageMeterAdmin', type: 'link', name: '', icon: 'av_timer' },
  {state: 'electricBillAdmin', type: 'link', name: '', icon: 'receipt'},
  {state: 'statisticAdmin', type: 'link', name: '', icon: 'assessment'},
  {state: 'powerCutAdmin', type: 'link', name: '', icon: 'power_off'},
  {state: 'SystemSetting', type: 'link', name: '', icon: 'settings'},
]; 

@Injectable()

export class MenuItems {
  getMenuitem(): Menu[] {
    return MENUITEMS;
  }

}
